import { Component, OnInit, EventEmitter } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MaterializeAction } from 'angular2-materialize';

import { PatientsService } from '../patients.service';
import { PersonsService } from '../../persons/persons.service';

import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { FormDataService } from '../../shared/form-data.service';
import { FileSelectService } from '../../shared/file-select.service';
import { PaginationService } from '../../shared/pagination.service';
import { HelpPanelService } from '../../shared/help-panel.service';
 
import { Patient, PatientForm } from '../patient';
import { Person } from '../../persons/person';

@Component({
  selector: 'app-patient-add',
  templateUrl: './patient-add.component.html',
  styleUrls: ['./patient-add.component.css']
})
export class PatientAddComponent implements OnInit {
  public addForm: FormGroup;
  public submitted: boolean = false;
  public patient: PatientForm = new PatientForm();
  public coincidencePersons: Person[];
  public datePickerOptions: any;
  public modalFoundActions = new EventEmitter<string|MaterializeAction>();
  public foundItem: any = {};

  constructor(
    private _patientsService: PatientsService,
    private _personsService : PersonsService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService,
    private _formDataService: FormDataService,
    private _fileSelectService: FileSelectService,
    private _pager: PaginationService,
    private _helpPanel: HelpPanelService,
    private _router: Router
  ) { }

  ngOnInit() {
    this._pager.notShow();
    this._helpPanel.setAnchor('search', {
      count: 0
    });

    this._toolbar.change({
      title: "Добавить пациента"
    });

    let picture = this._fileSelectService.get();

    if(this._formDataService.check()) {
      this.patient.setParams(this._formDataService.get());
      this._formDataService.clear();
    }

    this.datePickerOptions = {
      selectMonths: true,
      selectYears: 50,
      monthsFull: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
      monthsShort: ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Ин', 'Ил', 'Авг', 'Сент', 'Окт', 'Ноя', 'Дек'],
      weekdaysFull: ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота'],
      weekdaysShort: ['Вск', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
      today: 'Сегодня',
      clear: 'x',
      close: 'Закрыть',
      format: 'dd.mm.yyyy'
    };
    
    this.addForm = new FormGroup({
      amb_number: new FormControl('', [<any>Validators.required]),
			active: new FormControl(''),
			personId: new FormControl(''),
      person: new FormGroup({
        first_name: new FormControl(''),
        second_name: new FormControl(''),
        last_name: new FormControl(''),
        gender: new FormControl(''),
        birthdate: new FormControl(''),
        phone: new FormControl(''),
        email: new FormControl(''),
        picture: new FormControl('')
      })
    });

    this.addForm.patchValue(this.patient);
    this.addForm.patchValue({person: {picture}});
  }

  searchCoincidence() {
    let params = this.addForm.value.person;
    if(params.last_name || params.first_name || params.phone || params.email) {
      this._personsService.get(this.addForm.value.person)
        .subscribe((response) => {
          this.coincidencePersons = response.items;
          this._helpPanel.getAnchor('search').count = response.count;
        });
    }
  }

  setPerson(item) {
    this.patient.setParams({person: item});
    this.addForm.patchValue({personId: item._id});
    this.addForm.patchValue({person: item});
    this.addForm.get('person').disable();
  }

  selectImage() {
    this._formDataService.set(this.addForm.value);
    this._fileSelectService.clear();
    this._fileSelectService.single();
    this._fileSelectService.start();
    this._fileSelectService.setAction(() => this._router.navigate(['/patients/add']));
    
    if(this.addForm.value.person.picture) {
      this._fileSelectService.add(this._formDataService.get().person.picture);
    }

    this._router.navigate(['/uploads']);
  }

  closeModal() {
    this.addForm.controls['person'].enable();
    this.addForm.controls['personId'].reset();
    this.addForm.controls['person'].reset();
    this.modalFoundActions.emit({action:"modal",params:['close']});
  }

  onSubmit(values: any, valid: boolean) {
    this.patient.setParams(values);

    if(valid) {
      this._patientsService.add(this.patient)
        .subscribe(
          (data) => this._toastService.toast("Пациент успешно добавлен!"),
          (err) => {
            this.foundItem = err.item;
            console.log(this.foundItem);
            this.modalFoundActions.emit({action:"modal",params:['open']})
          }
        );
    }
  }

}



