import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { HelpPanelComponent } from '../shared/help-panel/help-panel.component';

import { PatientsComponent } from './patients/patients.component';
import { PatientAddComponent } from './patient-add/patient-add.component';
import { PatientUpdateComponent } from './patient-update/patient-update.component';

import { PatientsService } from './patients.service';
import { PersonsService } from '../persons/persons.service';

const PATIENTS_ROUTES: Routes = [
  { path: '', component: PatientsComponent },
  { path: 'add', component: PatientAddComponent },
  { path: 'update/:id', component: PatientUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(PATIENTS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule
  ],
  declarations: [PatientsComponent, PatientAddComponent, PatientUpdateComponent],
  providers: [PatientsService, PersonsService],
  exports: [PatientsComponent]
})
export class PatientsModule { }



