import { Person, IPerson } from '../shared/person';

export class IPatient {
    _id?: string;
    reg_address: string;
    amb_number: number;
	oms: {
		series: String,
		number: String
	};
	snils: string; // СНИЛС
	insurans: string; // Наименование страховой организации
	benefit_code: string; // Код категории льготы
	passport: { // Документ (паспорт, миграционная карта)
		type: string,
		series: string,
		number: string,
		issued: string,
		issued_date: Date
	};
	family: { // Семейное положение
		status: string,
		childs: string[],
		parent: string[]
	};
    personId: string;
    createdAt: Date;
    updatedAt: Date;
    active: boolean;
}
export class Patient implements IPatient {
    _id?: string;
    public reg_address: string;
    public amb_number: number;
	public oms: {
		series: String,
		number: String
	};
	public snils: string; 
	public insurans: string; 
	public benefit_code: string;
	public passport: {
		type: string,
		series: string,
		number: string,
		issued: string,
		issued_date: Date
	};
	public family: {
		status: string,
		childs: string[],
		parent: string[]
	};
    public personId: string;
    public createdAt: Date;
    public updatedAt: Date;
    public active: boolean;

    constructor(object?: any) {
        this.default();

        if(object != undefined) {
            this.setParams(object);
        }
    }

    clear(): void {
        this.default();
    }

    setParams(object: any) : void {
        this._id = object._id ? object._id : null;
        this.reg_address = object.reg_address ? object.reg_address : null;
        this.amb_number = object.amb_number ? object.amb_number : null;
        if(object.oms) {
            this.oms.series = object.oms.series ? object.oms.series : null;
            this.oms.number = object.oms.number ? object.oms.number : null;
        }
        this.snils = object.snils ? object.snils : null; 
        this.insurans = object.insurans ? object.insurans : null; 
        this.benefit_code = object.benefit_code ? object.benefit_code : null;
        if(object.passport) {
            this.passport.type = object.passport.type ? object.passport.type : null;
            this.passport.series = object.passport.series ? object.passport.series : null;
            this.passport.number = object.passport.number ? object.passport.number : null;
            this.passport.issued = object.passport.issued ? object.passport.issued : null;
            this.setDate(this.passport.issued_date, object.passport.issued_date);
        }
        if(object.family) {
            this.family.status = object.family.status ? object.family.status : null;
            this.family.childs = object.family.childs ? object.family.childs : [];
            this.family.parent = object.family.parent ? object.family.parent : [];
        }
        this.personId = object.personId ? object.personId : null;
        this.setDate(this.createdAt, object.createdAt);
        this.setDate(this.updatedAt, object.updatedAt);
        this.active = object.active ? true : false;
    }

    private setDate(self, date) {
        if(date) {
            if(date.search(/\d{2}.\d{2}.\d{4}/i) == -1) {
                self = date;
            } else {
                let [day, month, year] = date.split('.');
                self = new Date(year, month - 1, day, 0, 0, 0);
            }
        }
    }

    protected default() : void {
        this._id = null;
		this.reg_address = null;
        this.amb_number = null;
        this.oms = {
            series: null,
            number: null
        };
        this.snils = null; 
        this.insurans = null; 
        this.benefit_code = null;
        this.passport = {
            type: null,
            series: null,
            number: null,
            issued: null,
            issued_date: null
        };
        this.family = {
            status: null,
            childs: [],
            parent: []
        };
        this.personId = null;
        this.createdAt = null;
        this.updatedAt = null;
        this.active = true;
    }
}
export class PatientForm extends Patient {
    public person: IPerson;

    constructor(object?: any) {        
        super(object);
    }

    public setParams(object: any) : void {
        super.setParams(object);
        if(object.person) {
            this.person = new Person(object.person);
        }
    }

    protected default() : void {
        super.default();
        this.person = new Person();
    }
}



