import { Injectable } from '@angular/core';

@Injectable()
export class HelpPanelService {
  public anchor: any;
  public show: string = 'inactive';

  constructor() {}

  getAnchor(name) {
    return this.anchor ? this.anchor : false;
  }

  setAnchor(name, options?) {
    options = options ? options : {};
    options.icon = options.icon ? options.icon: name; 
    this.anchor = options;
  }

  toggle() {
    if(this.show == 'active') this.show = 'inactive';
    else this.show = 'active';
  }

}



