import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { Patient } from '../patient';
import { PatientsService } from '../patients.service';


import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { PaginationService } from '../../shared/pagination.service';
import { MaterializeAction } from 'angular2-materialize/src';
import { HelpPanelService } from '../../shared/help-panel.service';

@Component({
  selector: 'app-patients',
  templateUrl: './patients.component.html',
  styleUrls: ['./patients.component.css']
})
export class PatientsComponent implements OnInit {
  public patients: Patient[];
  public currentItem: Patient;
  public modalDeleteActions = new EventEmitter<string|MaterializeAction>();

  constructor(
    private _patientsService: PatientsService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService,
    private _pager: PaginationService,
    private _router: Router,
    private _helpPanel: HelpPanelService
  ) { }

  ngOnInit() {
    this._toolbar.change({
      title: "Список карт пациентов",
      name: "patients",
      actions: {
        add: () => this._router.navigate(['patients/add'])
      },
      actionsPosition: {
        extend: ['add'] 
      }
    });

    this._patientsService.get()
      .subscribe((data) => {
        this.patients = data.items;
      });
  }

  openModal(item) {
    this.currentItem = item;
    this.modalDeleteActions.emit({action:"modal",params:['open']});
  }

  closeModal() {
    this.modalDeleteActions.emit({action:"modal",params:['close']});
  }

  deleteItem(item) {
    let self = this;

    this._patientsService.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.patients.find((user, index) => {
            if(item._id == user._id) {
              self.currentItem = null;
              self.patients.splice(index, 1);
              return true;
            }
          });
          this._toastService.toast("Карта пациента удалена");
        }
      });
  }

}



