import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { IPatient, Patient, PatientForm } from './patient';

@Injectable()
export class PatientsService {
  private _baseUrl: string = "/api/v1/patients";

  constructor(private _http: Http) { }

  get(params?: any) : Observable<any> {
    let query = new URLSearchParams();
    if(params) {
      if(params.nPerPage) query.set('nPerPage', params.nPerPage);
      if(params.currentPage) query.set('pageNumber', params.currentPage);
    }
    return this._http.get(this._baseUrl, {search: query})
        .map( (res: Response) => res.json() )
        .catch( (error: any) => Observable.throw(error.json().errors || "Server error"));
  }

  getOne(id: string) : Observable<PatientForm> {
    return this._http.get(`${this._baseUrl}/${id}`)
        .map((res: Response) => res.json())
        .catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
  }

  add(body: IPatient) : Observable<IPatient> {
    let bodyString = JSON.stringify(body);
    let headers = new Headers({'Content-type': 'application/json'});
    let options = new RequestOptions({headers: headers});

    return this._http.post(this._baseUrl, JSON.stringify(body), options)
        .map((res: Response) => res.json())
        .catch((error: any) => Observable.throw(error.json() || 'Server error'));
  }

  update(body: IPatient) : Observable<IPatient> {
    let bodyString = JSON.stringify(body);
    let headers = new Headers({'Content-type': 'application/json'});
    let options = new RequestOptions({headers: headers});

    return this._http.put(`${this._baseUrl}/${body._id}`, JSON.stringify(body), options)
        .map((res: Response) => res.json())
        .catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
  }

  remove(id: string) : Observable<Patient> {
    let headers = new Headers({'Content-type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    
    return this._http.delete(`${this._baseUrl}/${id}`)
        .map((res: Response) => res.json())
        .catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
  }

}



